import React from '../../node_modules/react';
import './Bgm.css';

// BGM文件
import bgmFile from './resource/1.mp3'

class Bgm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      bgmName: 'Bgm', // BGM 样式名称
      bgmIcon: 'data:image/png;base64,', // BGM图标
    };

    /**
     * BGM 切换
     */
    this.bgmSwitch = () => {
      switch (document.querySelector('.bgmEl').paused){
        case true:
          document.querySelector('.bgmEl').load();
          document.querySelector('.bgmEl').play();
          this.setState({bgmName: 'Bgm open'})
          break;
        case false:
          document.querySelector('.bgmEl').pause();
          this.setState({bgmName: 'Bgm'})
          break;
        default:
          this.setState({bgmName: 'Bgm'})
      }
    }

    /**
     * BGM 初始化
     */
    this.bgmLoad = () => {
      this.setState({bgmName: 'Bgm open'})
    }
  }

  render() {
    const ta = {backgroundImage: `url(${this.state.bgmIcon})`}
    return (
        <div className={this.state.bgmName} style={ta}  onTouchEnd={this.bgmSwitch}>
          <audio className="bgmEl" src={bgmFile} onCanPlay={this.bgmLoad} autoPlay loop></audio>
        </div>
    );
  }
}

export default Bgm;