
import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ opacityValue: 1 })
    }, 500)
  }

  componentWillUnmount() {
    this.setState({ opacityValue: 0 })
  }

  render() {

    const IMAGE_LIST = [
      require('./Images/15.jpg'),
      require('./Images/16.jpg'),
    ];

    const AMI = {
      opacity: this.state.opacityValue
    };

    const TITLE = '家';

    const DESC = '每个温馨的家庭，都会让人的心不由升起羡慕的情感。';
    const DESC1 = '也许只是一句温馨的话语、一场温馨的画面……无不在宣誓着家庭的幸福。';

    const CSS_LIST = [
      {
        width: '100%',
        height: '27%',
        position: 'relative',
        alignItems: 'center',
        boxSizing: 'border-box',
      },
      {
        width: '100%',
        height: '73%',
        display: 'flex',
        marginBottom: '50px',
        position: 'relative',
        alignItems: 'center',
        boxSizing: 'border-box',
        flexDirection: 'column',
        justifyContent: 'spaceBetween',
      },
      {
        top: 0,
        width: '100%',
        height: '120%',
        position: 'absolute',
        boxSizing: 'border-box',
        border: '2px solid #333',
      },
      {
        fontSize: '1.5em',
        fontWeight: 'bold',
        textAlign: 'center',
        position: 'relative',
        fontFamily: 'KaiTi, KaiTi_GB2312',
        
      },
      {
        marginTop: '10px',
        textIndent: '2em',
        position: 'relative',
      },
      {
        width: '83%',
        height: '50%',
        marginBottom: '1vw',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        width: '83%',
        height: '50%',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
      {
        height: '90%',
        position: 'absolute',
        padding: '6vw 3vw 3vw 3vw',
      },
    ]

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]}>
          <div style={CSS_LIST[2]}></div>
          <div style={CSS_LIST[7]}>
            <div style={CSS_LIST[3]}>{TITLE}</div>
            <div style={CSS_LIST[4]}>{DESC}</div>
            <div style={CSS_LIST[4]}>{DESC1}</div>
          </div>
        </div>
        <div style={CSS_LIST[1]}>
          <div style={CSS_LIST[5]} className='anim-cross-to-square'></div>
          <div style={CSS_LIST[6]} className='anim-fade-star'></div>
        </div>
      </div>
    );
  }
}

export default Page;