
import React from 'react';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const TEXT = '请在手机环境下打开';
    const CSSDATA = {
        width: '100%',
        height: '100%',
        display: 'flex',
        fontSize: '30px',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
    }
    return (
      <div className="App" style={CSSDATA}>{TEXT}</div>
    );
  }
}

export default App;