import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0,
    };
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({opacityValue: 1});
    },500);
  }

  componentWillUnmount() {
    this.setState({opacityValue: 0});
  }

  render() {
    
    const AMI = {
      opacity: this.state.opacityValue
    };
    
    const TITLE = '回忆';
    
    const DESC = '每个温馨的家庭，都会让人的心不由升起羡慕的情感。也许只是一句温馨的话语、一场温馨的画面……无不在宣誓着家庭的幸福。';
    
    const IMAGE_LIST = [
      require('./Images/1.jpg')
    ];
    
    const CSS_LIST = [
      {
        margin: '7vw',
        display: 'flex',
        marginTop: '45vw',
        height: '31.7641vw',
        flexDirection: 'row',
      },
      {
        width: '20vw',
        height: '80%',
        display: 'flex',
        fontSize: '10vw',
        textAlign: 'left',
        alignSelf: 'center',
        flexDirection: 'row',
        alignItems: 'flex-end',
        justifyContent: 'center',
        writingMode: 'vertical-lr', 
      },
      {
        height: '80%',
        margin: ' 0 5vw',
        alignSelf: 'center',
        borderRight: '2px dotted #999',
      },
      {
        width: '48vw',
        fontSize: '12px',
        height: '16.617647vw',
        alignSelf: 'flex-end',
        marginBottom: '3.17641vw',
      },
      {
        margin: '7vw',
        width: '86vw',
        height: '86vw',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      }
    ];

    return (
        <div className="item" style={AMI}>
            <div style={CSS_LIST[0]}>
              <div style={CSS_LIST[1]}>{TITLE}</div>
              <div style={CSS_LIST[2]}></div>
              <div style={CSS_LIST[3]}>{DESC}</div>
            </div>
            <div style={CSS_LIST[4]}></div>
        </div>
    );
  }
}

export default Page;