
import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({opacityValue: 1})
    },500)
  }

  componentWillUnmount() {
    this.setState({opacityValue: 0})
  }

  render() {

    const AMI = {
      opacity: this.state.opacityValue
    };
    
    const TEXT = '为我们遮风挡雨，给我们无穷的力量。';
    
    const TITLE = `家 `;
    
    const IMAGE_LIST = [
      require('./Images/4.jpg'),
      require('./Images/5.jpg'),
    ];
    
    const CSS_LIST = [
      {
        width: '100vw',
        backgroundSize: 'cover',
        height: '36.8849%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
        transform: 'rotate(-5deg) translate(-12px, 14px)',
      },
      {
        width: '100vw',
        textAlign: 'center',
        height: '8.26587%',
      },
      {
        width: '100vw',
        backgroundSize: 'cover',
        height: '41.765873015873%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        transform: 'rotate(-7deg) translate(4vw, -3vw)',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
      {
        display: 'inline-block',
        fontFamily: 'KaiTi, KaiTi_GB2312',
      }
    ];

    return (
        <div className="item" style={AMI}>
          <div style={CSS_LIST[0]} className='anim-fade-right'></div>
          <div style={CSS_LIST[1]}>
            <h2 style={CSS_LIST[3]}>{TITLE}</h2>
            <span className='text-auto-type'>{TEXT}</span>
          </div>
          <div style={CSS_LIST[2]} className='anim-fade-left'></div>
        </div> 
    );
  }
}

export default Page;
