
import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({opacityValue: 1})
    },500)
  }

  componentWillUnmount() {
    this.setState({opacityValue: 0})
  }

  render() {

    const IMAGE_LIST = [
      require('./Images/13.jpg'),
      require('./Images/14.jpg'),
    ];
    
    const AMI = {
      opacity: this.state.opacityValue,
      width: '90vw',
      height: '90vh',
      margin: 'auto'
    };
    const TEXT = '为我们遮风挡雨，给我们无穷的力量。';
    
    const CSS_LIST = [
      {
        width: '80%',
        margin: 'auto',
        backgroundSize: 'cover',
        height: '51.884920634921%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        width: '100%',
        margin: 'auto',
        height: '10vh',
        textAlign: 'center',
      },
      {
        width: '80%',
        margin: 'auto',
        backgroundSize: 'cover',
        height: '41.765873015873%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      }
    ];

    return (
        <div className="item" style={AMI}>
          <div style={CSS_LIST[0]} className='anim-fade-heart'></div>
          <div style={CSS_LIST[1]}>
            <h2 className='text-kaiti' style={{
              display: 'inline-block'
            }}>家</h2>
            {TEXT}
          </div>
          <div style={CSS_LIST[2]} className='anim-fade-heart'></div>
        </div> 
    );
  }
}

export default Page;
