import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ opacityValue: 1 })
    }, 500)
  }

  componentWillUnmount() {
    this.setState({ opacityValue: 0 })
  }

  render() {

    const IMAGE_LIST = [
      require('./Images/8.jpg')
    ];

    const AMI = {
      opacity: this.state.opacityValue
    };

    const TITLE = '家';

    const DESC = '也许就是一种期盼，是每一天下班后放弃一切急匆匆赶回去的地方，有时候，无论你走多远在外过多么舒适的生活，你都会牵挂着它，即使远在天边你也会想到立刻回去的地方。';

    const CSS_LIST = [
      {
        height: '50%',
        width: '110%',
        position: 'relative',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        transform: 'translate(-9vw, 5vh)',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        width: '100%',
        height: '38%',
        display: 'flex',
        padding: '20px',
        alignItems: 'center',
        position: 'relative',
        boxSizing: 'border-box',
        flexDirection: 'row-reverse',
        justifyContent: 'flex-start',
      },
      {
        height: '140px',
        fontSize: '14vw',
        position: 'relative',
        writingMode: 'vertical-lr',
      },
      {
        height: '30vh',
        fontSize: '14px',
        position: 'relative',
        letterSpacing: '3px',
        writingMode: 'vertical-rl',
      },
    ];

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]} className='anim-fan-out'></div>
        <div style={CSS_LIST[1]}>
          <span style={CSS_LIST[2]} className='text-kaiti'>{TITLE}</span>
          <span style={CSS_LIST[3]}>{DESC}</span>
        </div>
      </div>
    );
  }
}

export default Page;