import React from 'react';
import './page.css';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({opacityValue: 1})
    },500)
  }

  componentWillUnmount() {
    this.setState({opacityValue: 0})
  }

  render() {
    
    const IMAGE_LIST = [
      require('./Images/21.jpg'),
      require('./Images/20.jpg'),
    ];
    
    const AMI = {
      opacity: this.state.opacityValue
    };
    
    const TITLE = '家';
    
    const DESC = '灵魂的延续的地方。';
    
    const CSS_LIST = [
      {
        left: '20vw',
        width: '60vw',
        height: '70vh',
        position: 'absolute',
        top:'5.8823529411765vw',
        border: '2px dashed #999',
      },
      {
        fontSize: '7vw',
        top:'98.1765vw',
        position: 'absolute',
        left: '9.7058823529412vw',
        width: '9.7058823529412vw',
        writingMode: 'vertical-lr', 
        height: '19.705882352941vw',
      },
      {
        top:'115.176vw',
        display: 'flex',
        fontSize: '12px',
        overflow: 'hidden',
        alignItems: 'center',
        position: 'absolute',
        flexDirection: 'row',
        letterSpacing: '2px',
        left: '9.7058823529412vw',
        writingMode: 'vertical-lr', 
        width: '9.7058823529412vw',
        height: '37.794117647059vw',
        justifyContent: 'flex-start',
      },

      {
        height: '40vh',
        position: 'absolute',
        backgroundSize: 'cover',
        top:'9.7058823529412vw',
        left: '7.2058823529412vw',
        width: '59.852941176471vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        height: '35vh',
        position: 'absolute',
        backgroundSize: 'cover',
        top:'90.441176470588vw',
        left: '27.058823529412vw',
        width: '58.970588235294vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
    ];

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]} className='border-auto-scroll'></div>
        <div style={CSS_LIST[1]} className='text-kaiti'>{TITLE}</div>
        <div style={CSS_LIST[2]} className='anim-fade-left'>{DESC} </div>
        <div style={CSS_LIST[3]} className='anim-cross-to-square '></div>
        <div style={CSS_LIST[4]} className='anim-fade-right'></div>
      </div>

    );
  }
}

export default Page;