import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({opacityValue: 1})
    },500)
  }

  componentWillUnmount() {
    this.setState({opacityValue: 0})
  }

  render() {
    
    const IMAGE_LIST = [
      require('./Images/6.jpg'),
      require('./Images/7.jpg'),
    ];
    
    const AMI = {
      opacity: this.state.opacityValue
    };
    
    const TITLE = '家';
    
    const DESC = '家人是风，吹走烦恼，留下舒爽。';

    const CSS_LIST = [
      {
        width: '100%',
        height: '27%',
        display: 'flex',
        position: 'relative',
        alignItems: 'center',
        boxSizing: 'border-box',
        flexDirection: 'column',
        justifyContent: 'center',
      },
      {
        height: '73%',
        width: '100%',
        display: 'flex',
        marginBottom: '50px',
        position: 'relative',
        alignItems: 'center',
        boxSizing: 'border-box',
        flexDirection: 'column',
        justifyContent: 'spaceBetween',
      },
      {
        top: 0,
        width: '100%',
        height: '120%',
        position: 'absolute',
        boxSizing: 'border-box',  
        border: '2px solid #333',
      },
      {
        fontSize: '14vw',
        position: 'relative',
      },
      {
        position: 'relative',
      },
      {
        width: '83%',
        marginBottom: '1vw',
        position: 'relative',
        backgroundSize: 'cover',
        height: '71.911764705882vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        width: '91%',
        marginTop: '2vh',
        position: 'relative',
        backgroundSize: 'cover',
        height: '55.117647058824vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
    ];

    return (
        <div className="item" style={AMI}>
          <div style={CSS_LIST[0]}>
            <div style={CSS_LIST[2]}></div>
            <div style={CSS_LIST[3]} className='text-kaiti'>{TITLE}</div>
            <div style={CSS_LIST[4]}>{DESC}</div>
          </div>
          <div style={CSS_LIST[1]}>
            <div style={CSS_LIST[5]} className='anim-cross-to-square'></div>
            <div style={CSS_LIST[6]} className='anim-cross-to-square'></div>
          </div>
        </div>
    );
  }
}

export default Page;