
import React from 'react';
import './page.css'

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ opacityValue: 1 })
    }, 500)
  }

  componentWillUnmount() {
    this.setState({ opacityValue: 0 })
  }

  render() {

    const IMAGE_LIST = [
      require('./Images/18.jpg'),
      require('./Images/19.jpg'),
    ];

    const AMI = {
      opacity: this.state.opacityValue
    };

    const TITLE = '拥有';

    const DESC = '一个温馨和美的家';
    const DESC1 = '一个灵魂歇息畅游的港湾';

    const CSS_LIST = [
      {
        top: 0,
        left: 0,
        width: '50%',
        height: '100%',
        position: 'absolute',
        background: '#EBEBEB',
      },
      {
        left: 0,
        height: '65vw',
        position: 'absolute',
        backgroundSize: 'cover',
        width: '71.7647058824vw',
        top: '11.764705882353vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        right: 0,
        width: '80vw',
        height: '40vh',
        position: 'absolute',
        backgroundSize: 'cover',
        top: '89.117647058824vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
      {
        top: '10vh',
        left: '2vw',
        fontSize: '7vw',
        position: 'absolute',
      },
      {
        left: '1vh',
        position: 'absolute',
        top: '29.117647058824vw',
      },
    ];

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]} className='six anim-fade-left'></div>

        {/* 一条竖线 */}
        <div style={{
          top: 0,
          left: '51%',
          width: 0,
          height: '100%',
          borderRight: '#EBEBEB solid',
          position: 'absolute',
        }} className='anim-fade-down'></div>
        <div style={CSS_LIST[1]} className='six photo anim-fade-left'></div>
        <div style={CSS_LIST[2]} className='six photo anim-fade-right'></div>

        {/* 右上方 文字 */}
        <div style={{
          position: 'absolute',
          right: 0,
          left: '73vw',
          height: '50%',
          writingMode: 'vertical-rl',
          letterSpacing: '2px'
        }}>
          <p style={CSS_LIST[3]} className='text-kaiti six text anim-fade-right'>{TITLE}</p>
          <p style={CSS_LIST[4]} className='six text anim-fade-right'>{DESC}</p>
        </div>

        {/* 左下方 文字 */}
        <div className='left-bottom-text-wrapper'>
          <p className='text-kaiti left-bottom-text-title six text anim-fade-left'>{TITLE}</p>
          <p className='left-bottom-text-desc six text anim-fade-left'>{DESC1}</p>
        </div>
      </div>
    );
  }
}

export default Page;