import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App/App.jsx';
import EMPTY_404 from './404/404.jsx';
import * as serviceWorker from './serviceWorker';

if(getIsWxClient()){
    ReactDOM.render(<App />, document.getElementById('root'));
}else{
    ReactDOM.render(<EMPTY_404 />, document.getElementById('root'));
}

/**
 * 判断是否是微信环境
 */
function getIsWxClient () {
    const UA = navigator.userAgent.toLowerCase();
    const UA_LIST = [ 'android', 'iphone', 'iPhone', 'iPad', 'ipad', 'micromessenger' ];
    return UA_LIST.filter(item => UA.includes(item)).length > 0 ? true : false ; 
};

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA

serviceWorker.unregister();