
import React from 'react';
import './App.css';

// 页面
import Bgm from '../Bgm/Bgm.jsx'
import One from '../pages/One/page.jsx'
import Two from '../pages/Two/page.jsx'
import Three from '../pages/Three/page.jsx'
import Four from '../pages/Four/page.jsx'
import Five from '../pages/Five/page.jsx'
import Six from '../pages/Six/page.jsx'
import Seven from '../pages/Seven/page.jsx'
import Eight from '../pages/Eight/page.jsx'
import Nine from '../pages/Nine/page.jsx'
import Ten from '../pages/Ten/page.jsx'
import Eleventh from '../pages/Eleventh/page.jsx'
import Twelve from '../pages/Twelve/page.jsx'
import Thirteen from '../pages/Thirteen/page.jsx'
import Fourteen from '../pages/Fourteen/page.jsx'
import Fifteen from '../pages/Fifteen/page.jsx'
import Sixteen from '../pages/Sixteen/page.jsx'


import IMG from '../pages/Eight/Images/13.jpg'
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      nowIndex: 0, // 当前页面索引
      boxLeft: 0, // 当前页面滑动距离
      touchLength: 0, // 当前页面滑动初始距离
      pages: [<One />, <Two />, <Three />, <Four />, <Five />, <Six />, <Seven />, <Eight />, <Nine />, <Ten />, <Eleventh />, <Twelve />, <Thirteen />, <Fourteen />, <Fifteen />, <Sixteen />] // 单页面列表
    };
    /**
     * 开始触摸绑定页面滑动初始值
     */
    this.touchStart = (e) => {
      this.setState({ touchLength: e.touches[0].clientX })
    }
    /**
     * 触摸滑动更改页面显示
     */
    this.touchMove = (e) => {
      this.setState({ boxLeft: e.touches[0].clientX - this.state.touchLength })
    }
    /**
     * 触摸结束动效逻辑
     */
    this.touchEnd = (e) => {
      // 触发上翻动画 // 触发左翻动画
      if (this.state.boxLeft < -(window.innerWidth / 3 * 2)) {
        window.requestAnimationFrame(this.stepUp);
      }
      // 触发归位动画
      else if (this.state.boxLeft > -(window.innerWidth / 3 * 2) && this.state.boxLeft < 0) {
        window.requestAnimationFrame(this.stepUn);
      }
      // 触发下翻动画 // 触发右翻动画
      else if (this.state.boxLeft > (window.innerWidth / 3 * 2)) {
        window.requestAnimationFrame(this.stepDown);
      }
      // 触发归位动画
      else if (this.state.boxLeft < (window.innerWidth / 3 * 2) && this.state.boxLeft > 0) {
        window.requestAnimationFrame(this.stepUn);

      }
    }
    /**
     * 向下滑动动效
     */
    this.stepDown = () => {
      this.setState({ boxLeft: this.state.boxLeft + 10 })
      if (this.state.boxLeft < document.querySelector('.Box').offsetHeight) {
        window.requestAnimationFrame(this.stepDown);
      }
      else {
        this.changePages(1);
      }
    }
    /**
     * 向上滑动动效
     */
    this.stepUp = () => {
      this.setState({ boxLeft: this.state.boxLeft - 10 })
      if (this.state.boxLeft > -document.querySelector('.Box').offsetHeight) {
        window.requestAnimationFrame(this.stepUp);
      }
      else {
        this.changePages(2);
      }
    }
    /**
     * 归位滑动动效
     */
    this.stepUn = () => {
      if (this.state.boxLeft > 0) {
        if (this.state.boxLeft - 10 < 0) {
          this.setState({ boxLeft: 0 })
        }
        else {
          this.setState({ boxLeft: this.state.boxLeft - 10 })
        }
      }
      else if (this.state.boxLeft < 0) {
        if (this.state.boxLeft + 10 > 0) {
          this.setState({ boxLeft: 0 })
        }
        else {
          this.setState({ boxLeft: this.state.boxLeft + 10 })
        }
      }
      if (this.state.boxLeft !== 0) {
        window.requestAnimationFrame(this.stepUn);
      }
      else {
        this.changePages(3);
      }
    }
    /**
     * 页面切换
     */
    this.changePages = (status) => {
      this.setState({ boxLeft: 0 })
      switch (status) {
        case 1:
          if (this.state.nowIndex === 0) {
            this.setState({ nowIndex: this.state.pages.length - 1 })
          }
          else {
            this.setState({ nowIndex: this.state.nowIndex - 1 })
          }
          break;
        case 2:
          if (this.state.nowIndex === this.state.pages.length - 1) {
            this.setState({ nowIndex: 0 })
          }
          else {
            this.setState({ nowIndex: this.state.nowIndex + 1 })
          }
          break;
        case 3:
          break;
        default:
          break;
      }
    }
    /**
     * 页面组件切换
     */
    this.Pages = () => {
      return this.state.pages[this.state.nowIndex];
    }
  }


  componentDidMount() {
    document.querySelector('audio').addEventListener("canplay", function(){//监听audio是否加载完毕，如果加载完毕，则读取audio播放时间
      alert(document.querySelector('audio').duration);
    });
    // var img =new Image();
    // img.onload =function(){
    //     img.onload =null;
    //     callback(img);
    // }
  }

  componentWillUnmount() {
  }

  render() {
    const ITEM_STYLE = {
      left: `${this.state.boxLeft}px`,
    }
    return (
      <div className="App">
        <Bgm />
        <div className="Box" style={ITEM_STYLE}
          onTouchStart={this.touchStart}
          onTouchMove={this.touchMove}
          onTouchEnd={this.touchEnd}
        >
          <this.Pages />
        </div>
      </div>
    );
  }
}

export default App;