
import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ opacityValue: 1 })
    }, 500)
  }

  componentWillUnmount() {
    this.setState({ opacityValue: 0 })
  }

  render() {

    const IMAGE_LIST = [
      require('./Images/25.jpg'),
      require('./Images/24.jpg'),
    ];

    const AMI = {
      opacity: this.state.opacityValue
    };

    const TITLE = '家——浓浓的爱语';

    const DESC = '有了灯，不再害怕夜晚没有星星和月亮；有了屋檐，不再害怕风吹和雨打；有了床，累了，困了，可以睡上甜甜的觉，做个美美的梦。';

    const CSS_LIST = [
      {
        width: '100%',
        height: '27%',
        position: 'relative',
        alignItems: 'center',
        boxSizing: 'border-box',
      },
      {
        width: '100%',
        height: '73%',
        display: 'flex',
        marginBottom: '50px',
        position: 'relative',
        alignItems: 'center',
        boxSizing: 'border-box',
        flexDirection: 'column',
        justifyContent: 'spaceBetween',
      },
      {
        top: 0,
        width: '100%',
        height: '120%',
        position: 'absolute',
        boxSizing: 'border-box',
        border: '2px solid #333',
      },
      {
        fontSize: '1.5em',
        fontWeight: 'bold',
        textAlign: 'center',
        position: 'relative',
        fontFamily: 'KaiTi, KaiTi_GB2312',
        
      },
      {
        textIndent: '2em',
        marginTop: '10px',
        position: 'relative',
      },
      {
        width: '83%',
        height: '50%',
        marginBottom: '1vw',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        width: '83%',
        height: '50%',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
    ]

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]}>
          <div style={CSS_LIST[2]}></div>
          <div style={{
            position: 'absolute',
            height: '90%',
            padding: '6vw 3vw 3vw 3vw',
          }}>
            <div style={CSS_LIST[3]}>{TITLE}</div>
            <div style={CSS_LIST[4]}>{DESC}</div>
          </div>
        </div>
        <div style={CSS_LIST[1]}>
          <div style={CSS_LIST[5]} className='anim-radius'></div>
          <div style={CSS_LIST[6]} className='anim-fade-heart'></div>
        </div>
      </div>
    );
  }
}

export default Page;