import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ opacityValue: 1 })
    }, 500)
  }

  componentWillUnmount() {
    this.setState({ opacityValue: 0 })
  }

  render() {

    const IMAGE_LIST = [
      require('./Images/26.jpg')
    ];

    const AMI = {
      opacity: this.state.opacityValue
    };

    const TITLE = '家';

    const DESC = '家是什么？家，是难舍的亲情，是温馨的集体，是充满了爱的世界，是一个永远温馨而动人的话题---在这里，父母用慈爱的伞为儿女撑起一方晴空，儿女用孝顺的心给父母奉上一杯香茶。';

    const CSS_LIST = [
      {
        left: '50%',
        width: '100%',
        height: '50%',
        borderRadius: '50%',
        position: 'relative',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        transform: 'translate(-50%, 5vh)',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        width: '100%',
        height: '38%',
        display: 'flex',
        padding: '20px',
        alignItems: 'center',
        position: 'relative',
        boxSizing: 'border-box',
        flexDirection: 'row-reverse',
        justifyContent: 'flex-start',
      },
      {
        height: '140px',
        fontSize: '14vw',
        position: 'relative',
        writingMode: 'vertical-lr',
      },
      {
        height: '30vh',
        fontSize: '14px',
        position: 'relative',
        letterSpacing: '3px',
        writingMode: 'vertical-rl',
      },
    ];

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]} className='anim-fade-star'></div>
        <div style={CSS_LIST[1]}>
          <span style={CSS_LIST[2]} className='text-kaiti'>{TITLE}</span>
          <span style={CSS_LIST[3]}>{DESC}</span>
        </div>
      </div>
    );
  }
}

export default Page;