import React from 'react';

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      opacityValue: 0
    };
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({opacityValue: 1})
    },500)
  }

  componentWillUnmount() {
    this.setState({opacityValue: 0})
  }

  render() {
    
    const IMAGE_LIST = [
      require('./Images/2.jpg'),
      require('./Images/3.jpg'),
    ];
    
    const AMI = {
      opacity: this.state.opacityValue
    };
    
    const TITLE = '家';

    const DESC = '幸福开始的地方。';

    const CSS_LIST = [
      {
        width: '52.2vw',
        position: 'absolute',
        top:'5.8823529411765vw',
        border: '2px dashed #999',
        left: '30.441176470588vw',
        height: '120.29411764706vw',
      },
      {
        fontSize: '7vw',
        top:'94.1765vw',
        left: '14.7588vw',
        position: 'absolute',
        width: '9.7058823529412vw',
        writingMode: 'vertical-lr', 
        height: '19.705882352941vw',
        fontFamily: 'KaiTi, KaiTi_GB2312',
      },
      {
        top:'95.176vw',
        display: 'flex',
        fontSize: '12px',
        overflow: 'hidden',
        left: '33.70588vw',
        position: 'absolute',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'flex-start',
      },
      {
        height: '77.5vw',
        position: 'absolute',
        backgroundSize: 'cover',
        top:'9.7058823529412vw',
        left: '7.2058823529412vw',
        width: '59.852941176471vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[0]})`,
      },
      {
        left: '30.0588vw',
        bottom: '20.4412vw',
        position: 'absolute',
        backgroundSize: 'cover',
        width: '58.970588235294vw',
        height: '49.264705882353vw',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundImage: `url(${IMAGE_LIST[1]})`,
      },
    ];

    return (
      <div className="item" style={AMI}>
        <div style={CSS_LIST[0]}></div>
        <div style={CSS_LIST[1]}>{TITLE}</div>
        <div style={CSS_LIST[2]}>{DESC} </div>
        <div style={CSS_LIST[3]} className='anim-circle'></div>
        <div style={CSS_LIST[4]} className='anim-triangle'></div>
      </div>
    );
  }
}

export default Page;